@import "../../styles/variables.scss";

.dictionary {
  height: 100vh;
}
.data-input {
  top: $menu-height;
  height: $input-container-height;
  box-shadow: 0px 1px 1px 1px #eee;
}
.search-input-form {
  width: 100%;
}
.form-control:focus {
  box-shadow: none;
  border: 2px solid teal;
}
.menu-container {
  background-color: $cyan;
  height: $menu-height;
}
.menu-title {
  font-size: 28px;
  color: white;
  font-weight: bold;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
